.App {
  text-align: center;
}

.Nav-styles{
  display: flex;
  padding: 3rem 0rem 0.5rem;
  justify-content: center;
  background-color: white;
  gap: 10rem;
}
.Nav-styles a{
  text-decoration: none;
  color: #333333;
  line-height: 1.75;
  font-weight: 600;
}

.App-body{
  padding: 0rem 3rem;
  background-color: white;
  min-height: 100vh;
  max-width: 1140px;
  margin: 0 auto;
}
.App-body h1{
  background-color: white;
  margin: 1rem 0rem 0rem;
  padding: 1rem;
}

.App-checkbox-cont{
  text-align: center;
  font-size: 18px;
  line-height: 24px;
  background-color: white;
}
.App-checkbox-heading{
  opacity: 0.8;
  font-weight: 500;
  /* text-decoration: underline; */
  margin-bottom: 8px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
